  --body-bg-dark: #202024;
  --violet: #4f2ee8;
  --yellow: #eac645;
  --white: #fff;
  --top-category-title: #b4afaf;
  --text-color: #111;
  --bgc-modal: #11111180;
  --scroll-color: #e0e0e0;
}

