.modal-wrapper {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: var(--bgc-modal);
  z-index: 500;
  transition: all var(--time-transform) var(--transform);
  place-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.modal-window-wrap {
  background-color: #0000;
  border: none;
  width: 339px;
  height: 810px;
  margin: auto;
  position: relative;
}

.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.no-scroll {
  overflow: hidden;
}

.modal-wrapper.is-open .modal-window {
  opacity: 1;
  transform: translate(0);
}

.modal-window {
  color: var(--text-color);
  border: 2px solid var(--text-color);
  background-color: var(--body-bg-light);
  opacity: 0;
  transition: all var(--time-transform) var(--transform);
  border-radius: 18px;
  height: auto;
  padding: 40px 24px;
  position: relative;
  transform: translate(0, -100%)perspective(600px)rotateX(90deg);
}

.modal-svg {
  stroke: var(--text-color);
  transition: all var(--time-transform) var(--transform);
}

.modal-svg:hover {
  filter: drop-shadow(0 0 1px var(--text-color));
  stroke: var(--violet);
}

.btn-modal-close {
  z-index: 2;
  transition: all var(--time-transform) var(--transform);
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  padding: 0;
  display: flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.btn-modal-close:is(:hover, :focus) .modal-svg, .modal-btn-main :is(:hover, :focus), .btn-modal-close:is(:hover, :focus) .modal-svg {
  scale: 1.5;
}

.modal-img-wrap {
  border: var(--text-color) 2px solid;
  border-radius: 8px;
  width: 100%;
  height: 408px;
  margin-bottom: 14px;
  display: flex;
  overflow: hidden;
}

.modal-title {
  letter-spacing: -.64px;
  margin-bottom: 4px;
  font-size: 16px;
  line-height: 1.125;
}

.modal-autor {
  letter-spacing: -.48px;
  color: var(--top-category-title);
  margin-bottom: 18px;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.17;
}

.modal-descr {
  letter-spacing: -.56px;
  height: 70px;
  margin-bottom: 8px;
  line-height: 1.29;
  overflow: auto;
}

.modal-shops {
  align-items: center;
  gap: 20px;
  width: 173px;
  display: flex;
}

.modal-img-shop {
  transition: all var(--time-transform) var(--transform);
}

.modal-link:hover .modal-img-shop {
  filter: drop-shadow(0 7px 2px #1113);
  scale: 1.3;
}

.modal-btn-wrapper {
  margin: 40px auto 0;
}

.modal-btn-main {
  letter-spacing: -.14px;
  text-transform: uppercase;
  color: var(--text-color);
  border: 2px solid var(--violet);
  transition: all var(--time-transform) var(--transform);
  background-color: #0000;
  border-radius: 40px;
  margin: auto;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
  display: block;
}

.modal-btn-main:hover {
  color: var(--white);
  background-color: var(--violet);
  box-shadow: 0 0 3px var(--violet);
  text-shadow: 0 0 3px var(--white);
  transition: all .1s var(--transform);
  scale: 1.05;
}

.modal-btn-main:active, .btn-modal-close:active .modal-svg {
  scale: 1;
}

.js-modal-remove {
  padding: 14px 20px;
}

.modal-bottom-text {
  letter-spacing: -.4px;
  text-align: center;
  color: var(--bgc-modal);
  opacity: 1;
  transition: all var(--time-transform) var(--transform);
  width: 242px;
  margin: 8px auto 0;
  font-size: 10px;
  line-height: 1.2;
